
<template>
<section>
  <Nav></Nav>
  <Hero></Hero>
  <div class="container">
      <hr>
  </div>  
  <div class="container">
    <div class="columns is-ancestor is-mobile category content is-multiline is-small is-gapless">
        <div class="column">
            <article class="tile is-child has-text-centered">
            <p><span class="iconfont icon-bike"></span></p>
            <h3 class="title">triathlon</h3>
            <p class="subtitle">triathlon</p>
            </article>
        </div>
        <div class="column">
            <article class="tile is-child has-text-centered">
            <p><span class="iconfont icon-runlog"></span></p>
            <h3 class="title">marathon</h3>
            <p class="subtitle">marathon</p>
            </article>
        </div>
        <div class="column">
            <article class="tile is-child has-text-centered">
            <p><span class="iconfont icon-cc-code"></span></p>
            <h3 class="title">coding</h3>
            <p class="subtitle">Code</p>
            </article>
        </div>
        <div class="column">
            <article class="tile is-child has-text-centered">
            <p><span class="iconfont icon-read"></span></p>
            <h3 class="title">reading</h3>
            <p class="subtitle">History, China, Science</p>
            </article>
        </div>
    </div>      
  </div>
  <div class="container">
      <hr>
  </div>  
  <section class="section">
      <div class="container is-medium">
        <h2 class="title is-3 has-text-centered has-text-weight-bold">Sponsors</h2>
            <div class="columns is-multiline sponsors is-mobile">            
                <div class="column"><p class="article"><img src="../assets/syb.jpg" alt="SYB"></p></div>
                <div class="column"><p class="article"><img src="../assets/scott.jpg" alt="Scott"></p></div>
                <div class="column"><p class="article"><img src="../assets/vision.png" alt="Vision"></p></div>
                <div class="column"><p class="article"><img src="../assets/xterra.png" alt="XTERRA"></p></div>
                <div class="column"><p class="article"><img src="../assets/on.png" alt="On Running"></p></div> 
            </div>
      </div>
  </section>
  <Footer></Footer>
</section>
</template>
<style>
    .sponsors{
        padding: 0.75rem;
    }
    .category .iconfont{
        font-size:80px;
        color: #888;
    }
    .category .title{
        text-transform: uppercase;
    }
    .sponsors .article{
        display: block;
        padding: 15px;
        transition-duration: 86ms;
        transition-property: all;
    }
    .sponsors .article:hover {
        transform: translateY(-0.5rem);
    }
    .sponsors .column{
        align-items: center;
        display: flex;
        justify-content: center;
    }
    .sponsors img{
        max-height:100px;
        max-width: 250px;
        opacity: .8;
        background-color: #eee;
    }
    .sponsors .article:hover img{        
        opacity: 1;
    }
    @media (max-width: 414px){
        .content{
            text-align: center;
        }
        img.hero-img{
            width: 128px;
        }
        .category .iconfont {
            font-size: 40px;
        }
        .content h3{
            font-size: 1.2em;
        }
        .subtitle{
            font-size: 1.2em;
        }
    }
</style>
<script>
  import Nav from '../components/nav'
  import Footer from '../components/footer'
  import Hero from '../components/hero'
  export default {
    watch: {
    },
    methods: {

    },
    components: {
      Nav,
      Hero,
      Footer
    }
  };
</script>



