<template>
<div class="container">
    <section class="hero is-medium">
        <div class="hero-body">
            <div class="container">
                <div class="columns is-vcentered">
                    <div class="column is-half has-text-centered">
                        <img src="../assets/logo256.jpg" class="hero-img" alt="migao" />
                    </div>
                    <div class="column content is-half">
                        <h1 class="title">
                            {{title}}
                        </h1>
                        <p class="subtitle">
                            {{subtitle}}
                        </p>
                        <router-link class="button" to="/about"> About me </router-link>
                    </div>
                </div>
            </div>
        </div>
    </section>
</div>
</template>
<style>
.hero-img{
    border-radius: 50%;
}    
</style>
<script>
export default {
  name: 'Hero',
  data(){
      return {
          title:'Hello!',
          subtitle:"My name is Migao, a fulltime web frontend developer and part time amateur triathlete."
      }
  }
}
</script>


