<template>
<section>
  <Nav></Nav>  
  <div class="section main">
    <div class="content gallery" v-if="photos.length>0">
      <div v-bind:key="index" v-for="(item,index) in photos"><img :src="item.src" alt></div>
    </div>
    <div class="container section loading" v-else><span class="loader"></span></div>   
  </div>
  <Footer></Footer>
</section>
</template>
<style>
.main{
  min-height: calc(100% - 357px);
}
.gallery { 
  padding: .5vw;
  font-size: 0;
  display: -ms-flexbox;
  flex-direction: column;
  flex-flow: row wrap; 
  display: -webkit-box;
  display: flex;
}
.gallery div { 
  -webkit-box-flex: auto;
  -ms-flex: auto;
  flex: auto; 
  width: 200px; 
  height: 200px;
  margin: .5vw; 
}
.gallery div img { 
  width: 100%; 
  height: 100%; 
  object-fit: cover;
}
.gallery div img:hover { 
    transform: scale(1.04);
    box-shadow: 2px 2px 6px #555;
}
@media screen and (max-width: 400px) {
  .gallery div { margin: 0; }
  .gallery { padding: 0; }  
}
</style>

<script>
  import Nav from '../components/nav'
  import Footer from '../components/footer'
  import Hero from '../components/hero'  
  import {getPhotoList} from '../api/index'
  export default {
    data() {
      return {
          photos:[]
      }
    },
    watch: {
    },
    methods: {
        fetchList(){
            getPhotoList( res=>{
                this.photos= res;
                //this.loadImageViewer();
            })
        },
        loadImageViewer(){
          HesGallery.setOptions({
            disableScrolling: false,
            hostedStyles: false,
            animations: true,
            minResolution: 0,

            showImageCount: true,
            wrapAround: true
          });
        }
    },
    components: {
      Nav,
      Footer
    },
    mounted(){
        this.fetchList();
    }
  };
</script>


