<template>
<section class="has-text-centered">
    <figure class="image is-64x64 container"><img class="is-rounded" src="../assets/dogs.jpg" alt="logo" /></figure>
    <div class="content">
        <h5>{{setting.name}}</h5>
        <p>{{setting.desc}}</p>
    </div>
    <hr />
    <nav class="cate">
        <span class="tag" data-tab="about" @click="changeTab" :class="currentTag=='about' ? 'on' : ''">关于我</span>
        <span class="tag" data-tab="sport" @click="changeTab" :class="currentTag=='sport' ? 'on' : ''">运动成绩</span>
        <span class="tag" data-tab="contact" @click="changeTab" :class="currentTag=='contact' ? 'on' : ''">业务联系</span>
    </nav>
</section>
</template>
<style>

.cate .tag{
    background-color:#f4f4f4;
    color: #4a4a4a;
    height: 2em;
    line-height:100%;
    padding:.5em 1em;
    margin:5px;
    font-size: 14px;
    border-radius: 20px;
    cursor: pointer;
}
.cate .tag.on{
  background-color:#d6dbdb;
}
</style>

<script>
import {getSetting} from '../api/index'
export default {
  name: 'About',
  data(){
      return {
          setting:{},
          currentTag:"about"
      }
  },
  methods:{
    fetchDetail(){
        getSetting((res)=>{
            if(res.length>0){
                this.setting=res[0];
            }
        })
    },
    changeTab(e){
        let tab=e.currentTarget.dataset.tab
        this.currentTag=tab
        this.$emit('loadAbout', tab)
    }
  },
mounted(){
    this.fetchDetail();
}
}
</script>


