<template>
  <section>
<footer class="footer">  
      <div class="container content has-text-centered">        
        <h5 class="is-5">Social Media</h5>
        <div class="columns is-centered">
          <div class="column is-6 is-12-mobile">
            <div class="columns is-gapless">
              <div class="column" :key="nav.li" v-for="nav in navs">      
                  <a :href="nav.url" class="navbar-item">
                    <span class="iconfont" :class="nav.class" :title="nav.li"> {{nav.li}}</span>
                  </a>
              </div>
            </div>
          </div>
        </div>
    <hr>
  <div class="content has-text-centered">
    <p>
      &copy; 2019 <router-link class="url" to="/">migao.io</router-link>
    </p>
  </div>
    </div>
</footer>
  </section>
</template>
<style>
  .url{
    text-transform: uppercase;
  }
</style>
<script>
export default {
  name: 'Footer',
  data(){
      return {
        navs:[{
            li: 'weibo',
            url: 'http://weibo.com/michaelinsh',
            class: 'icon-weibo'
        },
        {
            li: 'strava',
            url: 'https://www.strava.com/athletes/6133205',
            class: 'icon-strava'
        },
        {
            li: 'instagram',
            url: 'http://www.instagram.com/walkingp',
            class: 'icon-instagram'
        },
        {
            li: 'wechat',
            pic: '/static/img/wechat.png',
            url: 'javascript:void(0);',
            class: 'icon-wechat'
        },
        {
            li: '小程序',
            pic: '/static/img/miniP.jpg',           
            url: 'javascript:void(0);',
            class: 'icon-xcx'
        }]
      }
  },
  methods: {
  },
  computed: {
  }
}
</script>


